export default defineComponent({
    props: {
        wAttr: {
            type: Object,
            required: true,
            default: () => ({})
        },
        defaultValue: {
            type: String,
            default: ''
        }
    },
    setup(props, { emit }) {
        const cityProvinceAddress = province;
        const address = ref(props.defaultValue);
        const customSearch = (item, queryText, __) => {
            // const i1 = item.value.indexOf(queryText);
            const i2 = item.LC.indexOf(queryText.toLowerCase());
            const i3 = item.UC.indexOf(queryText.toUpperCase());
            return i2 > -1 || i3 > -1;
        };
        watch(() => address.value, () => {
            emit('data', address.value);
        });
        return {
            cityProvinceAddress,
            customSearch,
            address
        };
    }
});
